@use "../globals" as *;
@use "../parts/common" as *;

/*--------------------------------
ヘッダー
---------------------------------*/
.header {
  position: fixed;
  top: 0;
  height: 60px;
  z-index: 10;
  width: 100%;
  background-color: transparent;

  &.triggered {
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 0 5px 10px -6px rgba(0, 0, 0, 0.5);
  }

  @include mq(lg) {
    height: 80px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;

    @include mq(lg) {
      padding: 0 40px;
    }
  }

  &__logo {
    @include f_around(15, 700, 50, 15);

    @include mq(lg) {
      @include f_around(20, false, false, false);
    }
  }

  &__btn {
    position: relative;
    z-index: 40;
    width: 20px;
    height: 60px;
    border: none;
    outline: none;
    background: none;
    background-color: transparent;
    cursor: pointer;
    appearance: none;

    @include mq(lg) {
      width: 30px;
      height: 80px;
    }
  }

  &__line {
    display: block;
    position: relative;
    left: 0;
    width: 100%;
    height: 2px;
    transition: all 0.4s;
    border-radius: 4px;
    background-color: $cWhite;

    &::before, &::after {
      display: block;
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      transition: inherit;
      border-radius: 4px;
      background-color: $cWhite;
    }
    
    &::before {
      top: -8px;
    }

    &::after {
      top: 8px;
    }
  }
}

.gnav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 30;
  width: 100%;
  height: 100vh;
  transition: all 2s;
  opacity: 0;
  visibility: hidden;

  &__canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    z-index: 10;
    transition: all 1s ease-out;
    opacity: 0;
  }

  &__list {
    display: grid;
    place-items: center;
    row-gap: 5vh;
    position: absolute;
    z-index: 20;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    perspective: 2000px;
    transform-style: preserve-3d;
  }

  &__item {
    transform: translate3d(0, -100px, -1000px);
    opacity: 0;
    transition: transform 1s, opacity 1s;
    @include f_around(15, 700, 300, 15);

    @include mq(lg) {
      font-size: 1.2vw;
    }
  }
  
  &__link {
    text-decoration: none;
    color: $cWhite;
    display: inline-block;
    padding-bottom: 5px;

    @include mq(lg) {
      &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 50%;
        width: 0;
        height: 3px;
        transform: translateX(-50%);
        transition: all 0.3s;
        background-color: $cWhite;  
      }
  
      &:hover::after {
        width: 100%;
      }  
    }
  }

  &__circle-bg {
    position: fixed;
    z-index: 20;
    width: 100px; // 円形の始点
    height: 100px; // 円形の始点
    border-radius: 50%;
    background: linear-gradient(
      to top right, 
    $cMainPurple1,
    $cMainPurple2,
    $cMainPurple3,
    $cMainPurple4,
    );
    opacity: 0.8;
    transform: scale(0); // scaleで全体に拡大
    right: -50px;
    top: -50px;
    transition: transform 1s linear;
  }
}

.menu-open {

  & .header__line {
    background-color: transparent;
  }

  & .header__line::before,
  & .header__line::after {
    top: 0;
  }

  & .header__line::before {
    transform: rotate(45deg);
  }

  & .header__line::after {
    transform: rotate(-45deg);
  }

  & .gnav {
    opacity: 1;
    visibility: visible;

    &__canvas {
      opacity: 0.8;
    }

    &__item {
      transform: translate3d(0, 0, 0);
      opacity: 1;

      @for $i from 1 through 7 {
        &:nth-child(#{$i}) {
            transition-delay: $i * 0.07s;
        }
      }
    }

    &__circle-bg {
      transform: scale(25);

      @include mq(md) {
        transform: scale(50);
      }

      @include mq(md) {
        transform: scale(75);
      }

      @include mq(xl) {
        transform: scale(100);
      }
    }
  }
}

/*--------------------------------
メインビジュアル
---------------------------------*/
.mv {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: calc(100vh - 60px);
  position: relative;

  @include mq(lg) {
    height: calc(100vh - 80px);
  }

  #mvCanvas {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -10;
  }

  &__inner {
    padding: 0 20px;

    @include mq(lg) {
      padding: 0 40px;
    }
  }

  &__title {
    
    & svg {
      top: 0;
      left: 0;
      max-width: 100%;
      height: auto;
    }

    & path {
      fill-opacity: 0; // Snap.svgで透明度1
      fill: $cWhite;
      stroke: $cWhite;
      stroke-width: 1;
      stroke-miterlimit: 10;
    }
  }

  &__text {
    @include f_around(12, false, false, 20.4);
    margin-top: 40px;

    @include mq(lg) {
      @include f_around(16, false, false, 28.8);
    }
  }

  &__footer {
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
    width: 50px;
    height: 50px;
    z-index: 2;
  }

  &__scrolltext {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    color: $cWhite;
    font-size: 0.7rem;
    letter-spacing: 0.05em;

    &::after {
      content: "";
      position: absolute;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      width: 1px;
      background-color: $cWhite;

      @include animation(
        $name: down-arrow-anime,
        $iteration-count: infinite,
        $duration: 1.5s,
        $timing-function: ease-in-out
      );
    }
  }
}

@keyframes down-arrow-anime {
	0% {
		height: 0;
    transform: translate(-50%, 0);
		opacity: 0;
	}
	30% {
		height: 30px;
		opacity: 1;
	}
	100% {
		height: 0;
    transform: translate(-50%, 50px);
		opacity: 0;
	}
}

/*--------------------------------
Works
---------------------------------*/
.works {

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
    row-gap: 40px;
    
    @include mq() {
      padding: 60px 0;
      flex-direction: row;
      row-gap: 0;
    }

    &::after {
      position: absolute;
      bottom: -60px;
      left: -20px;
      width: calc(40px + 100%);
      height: 85%;
      z-index: -1;
      display: block;
      content: "";
      background-color: $cBgGray;
      transition: opacity 1.3s;
      opacity: 0;
      
      @include mq() {
        top: 50%;
        bottom: auto;
        left: auto;
        right: 0;
        width: 60%;
        height: 100%;
        transform: translateY(-50%);
      }
    }

    &.bg-active::after {
      opacity: 1;

      @include animation(
        $name: box-shadow,
        $duration: 0.4s,
        $timing-function: cubic-bezier(0.250, 0.460, 0.450, 0.940),
        $delay: 1.3s,
        $iteration-count: 1,
        $direction: normal,
        $fill-mode: forwards
      );
    }

    @include mq(lg) {
      &.bg-active .works__desc .heading-primary::before {
        @include animation(
          $name: scaleX,
          $duration: 0.4s,
          $timing-function: ease,
          $delay: 1.3s,
          $iteration-count: 1,
          $direction: normal,
          $fill-mode: forwards
        );
      }
    }
  }
  
  &__img-wrapper {
    width: 100%;
    overflow: hidden; // 背景の透過を消す
    background-color: $cBlack;
    
    @include mq() {
      width: 60%;
    }
  }

  &__desc {
    flex: 1;
    text-align: center;
    overflow: hidden;

    @include mq(lg) {
      & .heading-primary {
        display: inline-block;
        position: relative;
  
        &::before {
          display: block;
          position: absolute;
          content: "";
          height: 2px;
          width: 150px;
          top: 50%;
          left: -170px;
          background: $cWhite;
          transform: scaleX(0);
          transform-origin: right;
        }
      }
    }

    & .section__body {
      @include mq(md) {
        margin-top: 40px;
      }
    }
  }

  &__link {
    margin-top: 30px;
  }
}

@keyframes box-shadow {
  0% {
    box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    box-shadow: inset 0 0 18px 3px rgba(0, 0, 0, 0.7);
  }
}

@keyframes scaleX {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

/*--------------------------------
Skill
---------------------------------*/
.skill {
  position: relative;

  &__motion-path {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    width: 90vw;

    @include mq(sm) {
      width: 55vw;
    }

    @include mq(md) {
      top: 15%;
      width: 740px;
    }

    @include mq(lg) {
      width: 950px;
    }

    &.motion-path-active {
      & #skill-pc-path, & #skill-sp-path {
        stroke-opacity: 1;
      }
      
      & #skill-polygon {
        fill-opacity: 1;
      }
    }

    #skill-svg-pc, #skill-svg-sp {
      overflow: visible;
    }
    
    #skill-pc-path, #skill-sp-path {
      width: 100%;
      height: 100%;
      fill: none;
      stroke-opacity: 0;
      transition: all 2s;
    }

    #skill-pc-path {
      stroke: url(#skill-gradient-pc);
      stroke-width: 5;
    }

    #skill-sp-path {
      stroke: url(#skill-gradient-sp);
      stroke-width: 3;
    }

    #skill-polygon {
      fill: $cWhite;
      width: 7vw;
      height: 7vw;
      fill-opacity: 0;

      @include mq(md) {
        width: 3vw;
        height: 3vw;
      }

      @include mq(lg) {
        width: clamp(32px, 3vw, 64px); // 最小値,中間値,最大値
        height: clamp(32px, 3vw, 64px); // 最小値,中間値,最大値
      }
    }
  }

  #skill-gradient-stop1 {
    @include animation(
      $name: skill-animation-stop,
      $duration: 1.5s,
      $timing-function: ease-in-out,
      $delay: 0s,
      $iteration-count: infinite,
      $direction: alternate,
      $fill-mode: forwards
    );
  }
  
  #skill-gradient-stop2 {
    @include animation(
      $name: skill-animation-stop,
      $duration: 1.5s,
      $timing-function: ease-in-out,
      $delay: 0s,
      $iteration-count: infinite,
      $direction: alternate-reverse,
      $fill-mode: forwards
    );
  }

  &__list {
    display: grid;
    row-gap: 35px;
    
    @include mq(md) {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 4%;
    }

    @include mq(lg) {
      row-gap: 75px;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    column-gap: 20px;

    @include mq(lg) {
      column-gap: 25px; 
    }

    @include mq(xl) {
      column-gap: 30px;
    }
  }
  
  &__img {
    flex-basis: 50px;
    height: auto;
    opacity: 0;

    @include mq() {
      flex-basis: 60px;

      &:hover {
        @include animation(
          $name: skill-img-hover-vibrate,
          $duration: 0.3s,
          $timing-function: linear,
          $delay: 0s,
          $iteration-count: infinite,
          $direction: normal,
          $fill-mode: both
        );
      }
    }

    @include mq(xl) {
      flex-basis: 70px;
    }
  }

  &__body {
    flex: 1;
  }

  &__name {
    position: relative;
    overflow: hidden;
    display: inline-block;
    @include f_around(18, false, 50, 18);

    @include mq() {
      @include f_around(20, false, 70, 22);
    }

    @include mq(xl) {
      @include f_around(22, false, false, false);
    }

    & .rect {
      position: absolute;
      top: 0;
      left: 0;
      content: "";
      display: block;
      background: $cWhite;
      width: 100%;
      height: 100%;
      transform: translateX(-100%);
    }
  }

  &__text {
    @include f_around(12, false, false, 22.1);
    margin-top: 5px;

    @include mq() {
      @include f_around(14, false, false, 25.2);
      margin-top: 10px;
    }
  }
}

@keyframes skill-animation-stop {
  0% {
    stop-color: $cMainPurple1;
  }
  100% {
    stop-color: $cMainPurple4;
  }
}

@keyframes skill-img-hover-vibrate {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-2px, 2px);
  }
  40% {
    transform: translate(-2px, -2px);
  }
  60% {
    transform: translate(2px, 2px);
  }
  80% {
    transform: translate(2px, -2px);
  }
  100% {
    transform: translate(0);
  }
}

/*--------------------------------
About
---------------------------------*/
$about-width-lg: 700px;
$about-width-xl: 850px;

.about {
  position: relative;
  min-height: 100vh;

  &__inner {
    display: grid;
    margin: 0 auto;
    gap: 3vh 0;
    grid-template-rows: minmax(150px, 1fr) 2fr;
    opacity: 0;
    transition: opacity 1s;  

    @include mq(md) {
      gap: 5vh 0;
      grid-template-rows: minmax(150px, 1fr) 3fr;
    }

    @include mq(lg) {
      grid-template-columns: 1fr $about-width-lg;
      grid-template-rows: 1fr;
      gap: 0 30px;
      max-width: 900px;
    }

    @include mq(xl) {
      grid-template-columns: 1fr $about-width-xl;
      max-width: 1150px;
    }

    &.about-inner-active {
      opacity: 1;

      & .about__img-wrapper {
        @include animation(
          $name: about-img-wrapper-animation,
          $duration: 1s,
          $timing-function: ease,
          $delay: 0s,
          $iteration-count: 1,
          $direction: normal,
          $fill-mode: forwards
        );
      }
    }
  }

  &__img {
    border-radius: 50%;

    &-wrapper {
      width: 150px;
      margin: 0 auto;
      border-radius: 50%;

      @include mq(md) {
        width: auto;
        max-width: 180px;
      }
      
      @include mq(lg) {
        max-width: 300px;
      }
    }
  }

  &__list-wrapper {
    position: relative;
    overflow: hidden;
    height: 100%; // 高さは親のgrid-rowsで指定

    &.about-inview {
      opacity: 0;
      transition: opacity 1s;  
    }

    &.about-inview-active {
      opacity: 1;
    }

    @include mq(lg) {
      height: 60vh;
    }
  }

  &__list {
    position: absolute; // 横スクロール
    top: 0;
    left: 0;
    display: flex;
    column-gap: 60px;
    height: 100%;
  }

  &__item {
    height: 100%;
    color: $cWhite;
    background-color: $cBgGray;
    width: calc(100vw - 40px);
    position: relative;
    padding: 1.5vh 8vw 0;

    @include mq(md) {
      padding: 6vh 8vw 0;
    }

    @include mq(lg) {
      width: $about-width-lg; // about__innerと合わせる
      padding: 6vh 6vw 0;
    }

    @include mq(xl) {
      width: $about-width-xl; // about__innerと合わせる
    }

    &.box-shadow-active {
      @include animation(
        $name: box-shadow,
        $duration: 0.4s,
        $timing-function: cubic-bezier(0.250, 0.460, 0.450, 0.940),
        $delay: 0s,
        $iteration-count: 1,
        $direction: normal,
        $fill-mode: forwards
      );
    }

    &-content-number {
      font-size: 6.5rem;
      color: $cWhite;

      @include mq(md) {
        font-size: clamp(100px, 13vw, 150px); // 最小値,中間値,最大値
      }

      &-wrapper {
        position: absolute;
        top: 25%;
        left: 5%;
        z-index: -1;

        @include mq(lg) {
          top: 35%;
          left: 15%;
        }
  
        &.content-number-wrapper-active {
          @include animation(
            $name: about-number-animation,
            $duration: 1s,
            $timing-function: ease,
            $delay: 0s,
            $iteration-count: 1,
            $direction: normal,
            $fill-mode: forwards
          );
        }
      }
    }

    &-content-title {
      @include f_around(20, 700, 100, 30);
      display: inline-block;
      position: relative;

      @include mq(md) {
        @include f_around(24, false, false, false);
      }

      @include mq(lg) {
        font-size: clamp(24px, 2.4vw, 34px); // 最小値,中間値,最大値
      }

      &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 50%;
        width: 0;
        height: 2px;
        transform: translateX(-50%);
        transition: all 0.3s;
        background-color: $cWhite;  
      }
  
      &.underline-active::after {
        width: 100%;
      }
    }

    &-content-text {
      margin-top: 20px;
      @include f_around(12, false, false, 22.1);

      @include mq(md) {
        margin-top: 30px;
        @include f_around(18, false, false, 27);
      }

      @include mq(lg) {
        margin-top: 60px;
      }
    }

    &-content-text + &-content-text {
      margin-top: 10px;

      @include mq(md) {
        margin-top: 20px;
      }
    }
  }

  &__line {
    height: 2vh;
    width: 100%;
    background-color: rgba(125, 125, 125, 0.5);
    position: absolute;
    bottom: 5%;
    opacity: 0;
    transition: opacity 1s;

    &.line-active {
      opacity: 1;
    }
  
    &-inner {
      content: "";
      top: 0;
      left: 0;
      height: 2vh;
      // background-size: 300% 100%;
      background-image: linear-gradient(
        to right,
        $cMainPurple1,
        $cMainPurple2,
        $cMainPurple3,
        $cMainPurple4
      );
    }
  }
}

 @keyframes about-img-wrapper-animation {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}

@keyframes about-number-animation {
  0% {
    text-shadow: 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555;
    transform: translateX(0);
  }
  100% {
    text-shadow: -1px 0 #555555, -2px 0 #555555, -3px 0 #555555, -4px 0 #555555, -5px 0 #555555, -6px 0 #555555, -7px 0 #555555, -8px 0 #555555;
    transform: translateX(8px);
  }
}

/*--------------------------------
Contact
---------------------------------*/
.contact {
  text-align: center;

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 20px;
    width: 100%;
    height: 300px;
    overflow: hidden;
    box-shadow: inset 0 0 18px 3px rgba(0, 0, 0, 0.7);
  
    @include mq(lg) {
      height: 400px;
    }

    &.bg-active .contact__canvas {
      opacity: 0.9;
    }
  }

  &__canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    z-index: -1;
    transition: all 1s ease-out;
    opacity: 0;
  }

  &__message {
    position: relative;
    z-index: 2;
    text-align: center;
  }
  
  &__left, &__right {
    @include f_around(22, false, 100, false);
    color: $cWhite;
    white-space: nowrap;

    @include mq(lg) {
      @include f_around(32, false, 200, false);
    }
  }

  &__link {
    display: block;
    position: relative;
    z-index: 2;
  }
}

/*--------------------------------
ページトップ
---------------------------------*/
.page-top {
  font-weight: bold;
  padding: 10px 0;
  cursor: pointer;
  text-align: center;

  background-color: gray;

  & .material-icons-outlined {
    color: $cWhite;
    vertical-align: bottom;
  }
}

/*--------------------------------
フッター
---------------------------------*/
.footer {
  padding: 30px 0;

  @include mq() {
    padding: 40px 0;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 30px;

    @include mq() {
      column-gap: 40px;
    }
  }

  &__item {
    @include f_around(12, false, 100, 12);
    color: $cWhite;
    transition: .4s all cubic-bezier(0.37, 0, 0.63, 1);

    @include mq() {
      @include f_around(14, false, 150, 14);
    }

    &:hover {
      letter-spacing: 0.3em;
      opacity: 0.8;
    }
  }

  &__link {
    display: inline-block;
    position: relative;
    overflow-wrap: break-word;

    &-icon {
      position: absolute;
      font-size: 1em;
      top: 50%;
      left: -1.25em;
      transition: all 0.6s ease;
      opacity: 0;
      transform: translateX(-8px) translateY(-50%);

      &.appear.inview {
        opacity: 1;    
        transform: translateX(0) translateY(-50%);
      }
    }

    & .tween-animate-title {
      display: inline-block;
    }
  }

  &__copyright {
    margin-top: 40px;
    @include f_around(10, false, 100, 10);
    text-align: center;
    color: $cWhite;

    @include mq() {
      margin-top: 50px;
    }

    & .item {
      display: inline-block;
    }
  }
}

